const companiesItems = document.querySelectorAll('.companies__item');

const companiesSlider = new Swiper('.companies__track', {
    loop: true,
    freeMode: true,
    spaceBetween: 0,
    grabCursor: true,
    slidesPerView: companiesItems.length - 1,
    loop: true,
    autoplay: {
        delay: 1,
        disableOnInteraction: true
    },
    freeMode: true,
    speed: 3000,
    freeModeMomentum: false
});

const customersSlider = new Swiper('.customers__track', {
    effect: 'coverflow',
    grabCursor: true,
    centeredSlides: true,
    slidesPerView: 'auto',
    coverflowEffect: {
        rotate: 0,
        stretch: 0,
        depth: 100,
        modifier: 2,
        slidesShadow: true,
    },
    // loop: true
});
